<template>
    <div class="_sign">
        
        <div class="bbx">
            
            <div class="tit">
                OTT 
            </div>

            <div class="argee">
                <div class="cont">
                           .<br/>
                         . <br/>
                        .
                </div>
                <div class="chek">
                    <input type="checkbox" id="ag" :checked="chek" @change="chekHandler"> <label for="ag"></label>
                </div>
            </div>

            <div class="inb">
                <label for="name"></label>
                <input 
                    type="text" 
                    id="name" 
                    :value="signData.userName" 
                    placeholder=" " 
                    style="max-width: 250px;" 
                    @input="signData.userName = $event.target.value"
                    ref="name"
                >
            </div>

            <div class="inb">
                <label for="userEmail"></label>
                <input 
                    type="email" 
                    id="userEmail" 
                    :value="signData.userEmail" 
                    placeholder=" " 
                    @input="signData.userEmail = $event.target.value"
                    ref="userEmail"
                >
            </div>

            <div class="inb">
                <label for="userID"></label>
                <input 
                    type="text" 
                    id="userID" 
                    :value="signData.userID" 
                    placeholder=" " 
                    @input="signData.userID = $event.target.value"
                    ref="userID"
                >
            </div>

            <div class="inb">
                <label for="pw"></label>
                <input 
                    type="password" 
                    id="pw" 
                    :value="signData.userPW" 
                    placeholder=" " 
                    @input="signData.userPW = $event.target.value"
                    ref="pw"
                >
            </div>

            <div class="inb">
                <label for="pwc"> </label>
                <input 
                    type="password" 
                    id="pwc" 
                    :value="pwc" 
                    placeholder="  " 
                    @input="pwc = $event.target.value"
                    ref="pwc"
                >
            </div>

            <div class="btn">
                <button @click="submit"></button>
            </div>

        </div>

    </div>
</template>

<script>

    import { mapState } from 'vuex';

    export default {
        name : "Sign",
        data() {
            return {
                chek : false,
                signData : {
                    userName : "",
                    userEmail : "",
                    userID : "",
                    userPW : ""
                },
                pwc : ""
            }
        },
        computed : {
            ...mapState(['member'])
        },
        methods: {
            chekHandler(e){
                this.chek = e.target.checked;
            },
            submit(){

                const emailExp = new RegExp('[a-z0-9]+@[a-z]+\.[a-z]{2,3}'); //eslint-disable-line
                const pwExp = new RegExp('(?=.*[a-z])(?=.*[0-9])(?=.*[^A-Za-z0-9])(?=.{6,})');

                if(!this.chek){
                    return alert(' .');
                }

                if(this.signData.userName == ""){
                    this.$refs.name.focus();
                    return alert('  .');
                    
                }

                if(this.signData.userEmail == ""){
                    this.$refs.userEmail.focus();
                    return alert('  .');
                }

                if(!emailExp.test(this.signData.userEmail)){
                    this.$refs.userEmail.focus();
                    return alert('   .');
                }

                if(this.signData.userID == ""){
                    this.$refs.userID.focus();
                    return alert('  .');
                }

                if(this.signData.userPW == ""){
                    this.$refs.pw.focus();
                    return alert('  .');
                }

                if(!pwExp.test(this.signData.userPW)){
                    this.$refs.pw.focus();
                    return alert(' 6 ,    .');
                }

                if(this.pwc == ""){
                    this.$refs.pwc.focus();
                    return alert('  .');
                }

                if(this.pwc != this.signData.userPW){
                    this.signData.pw = "";
                    this.pwc = "";
                    return alert('  .');
                }

                const find = this.member.findIndex(e=>e.userID == this.signData.userID);
                if(find >= 0){
                    this.signData.userID = "";
                    this.$refs.userID.focus();
                    return alert('  .');
                }

                this.$store.commit('addMember',this.signData);
                alert(' .');
                this.$router.push('/auth');
                

            }
        },
    }
</script>

<style lang="scss">
    @import "./Sign.scss";
</style>