<template>
    <div class="login_layout">
        
        <div class="bbx">

            <div class="logo">
                <img src="@/assets/img/logo.svg" alt="">
            </div>

            <div class="tit">
                 .
            </div>
            
            <form @submit="login">

                <div class="inb">
                    <input 
                        type="text" 
                        :value="userId" 
                        placeholder=" "
                        @input="userId = $event.target.value"
                        ref="userId"
                    >
                </div>
                
                <div class="inb">
                    <input 
                        type="password" 
                        :value="userPw" 
                        placeholder=" "
                        @input="userPw = $event.target.value"
                        ref="userPw"
                    >
                </div>

                <div class="flex">
                    <div class="chek">
                        <input type="checkbox" id="save" @change="saveId" :checked="saveid"> <label for="save"> </label>
                    </div>

                    <router-link to="/auth/find"> /  </router-link>
                </div>
                
                <div class="btn">
                    <button type="submit"></button>
                </div>

            </form>
            
            <div class="banner" @click="$router.push('/auth/sign')">
                 ?
            </div>
            
        </div>

    </div>
</template>
<script>

    import {useCookies} from "vue3-cookies"

    export default {
        name : "Login",
        setup(){
            const { cookies } = useCookies();
            return { cookies };
        },
        data() {
            return {
                userId : "",
                userPw : "",
                saveid : false,
            }
        },
        mounted() {
            let idGet = this.cookies.get("myUser");
            if(idGet){
                this.userId = idGet;
                this.saveid = true;
            }
        },
        methods: {
            saveId(e){
                this.saveid = e.target.checked;
            },
            login(e){

                e.preventDefault();

                if(this.userId == ""){
                    this.$refs.userId.focus();
                    return alert(" .");
                }

                if(this.userPw == ""){
                    this.$refs.userId.focus();
                    return alert(" .");
                }

                const find = this.$store.state.member.findIndex(e=>e.userID == this.userId);

                if(find < 0 || this.userPw != this.$store.state.member[find].userPW){
                    return alert('   .')
                }

                if(this.saveid){
                    this.cookies.set("myUser", this.userId);
                }
                
                this.$store.commit('authLogin',this.userId);
                alert(' .');
                this.$router.push('/user/select');

            }
        },
    }
</script>

<style lang="scss">
    @import "./Login.scss";
</style>