<template>

    <div class="login_layout">
        
        <div class="bbx">

            <div class="logo">
                <img src="@/assets/img/logo.svg" alt="">
            </div>

            <div v-if="!setFind">

                <div class="tit">
                     ?
                </div>
                
                <form @submit="findID">

                    <div class="inb">
                        <input 
                            type="text" 
                            :value="inputName" 
                            placeholder="  "
                            @input="inputName = $event.target.value"
                        >
                    </div>
                    
                    <div class="inb">
                        <input 
                            type="email" 
                            :value="inputEmail" 
                            placeholder="  "
                            @input="inputEmail = $event.target.value"
                        >
                    </div>

                    <div class="flex">
                        <router-link to="/auth"></router-link>
                        <button type="button" @click="setFind = !setFind"></button>
                    </div>

                    <div class="btn">
                        <button type="submit"></button>
                    </div>

                </form>

            </div>

            <div v-if="setFind">

                <div class="tit">
                     ?
                </div>

                <form @submit="findPW">

                    <div class="inb">
                        <input 
                            type="text" 
                            :value="inputID" 
                            placeholder="  "
                            @input="inputID = $event.target.value"
                        >
                    </div>

                    <div class="inb">
                        <input 
                            type="email" 
                            :value="inputEmail" 
                            placeholder="  "
                            @input="inputEmail = $event.target.value"
                        >
                    </div>

                    <div class="flex">
                        <router-link to="/auth"></router-link>
                        <button type="button" @click="setFind = !setFind"></button>
                    </div>

                    <div class="btn">
                        <button type="submit"></button>
                    </div>

                </form>
            </div>

            <div class="banner" @click="$router.push('/auth/sign')">
                 ?
            </div>
            
        </div>

    </div>

</template>

<script>
    export default {
        name : "Find",
        data() {
            return {
                setFind : false,
                inputEmail : "",
                inputName : "",
                inputID : ""
            }
        },
        methods: {
            findID(e){
                e.preventDefault();

                const find = this.$store.state.member.filter(e=>e.userName == this.inputName && e.userEmail == this.inputEmail)[0];

                if(find == undefined){
                    return alert('  .');
                }

                alert(`${this.inputName}  ${find.userID} .`);
                this.$router.push('/auth');

            },
            findPW(e){
                e.preventDefault();

                const find = this.$store.state.member.filter(e=>e.userID == this.inputID && e.userEmail == this.inputEmail)[0];

                if(find == undefined){
                    return alert('  .');
                }

                alert(`${this.inputID}  ${find.userPW} .`);
                this.$router.push('/auth');

            }
        },
    }
</script>

<style>

</style>